import React, { Component } from 'react'
import { PersonalDataConsumer } from '../personalDataContext'
import ExperienceLayout from './Experience/ExperienceTemplate/ExperienceLayout'
import InlineImageText from './Template/InlineImageText'

class Experience extends Component {
    
    render() {
        // var obj =[];
        var experience= false;
        let lockImageLink="https://media.discordapp.net/attachments/549260114758991882/904943391920508948/unknown.png";
        if(experience){
            return (
                <div>
                    <ExperienceLayout />
                </div>
            )
        }else{
            return (
                <div>
                    <InlineImageText imageLink={lockImageLink} text="NO Expericence" width="18px" height="18px"/>
                </div>
            )
        }

    }
}

export default Experience


